/* App.css */

* {
  margin: 0;
  padding: 0;
  /* box-sizing: border-box; */
}

/* 전체 요소에 대한 스타일 */
body {  margin: 0;
  padding: 0;
  background-color: #fff;
  font-family: 'Nanum Gothic';
}
img:hover{opacity: 0.8;}

/* ul, li 요소에 대한 스타일 */
ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

/* a 태그에 대한 스타일 */
a {
  text-decoration: none;
}

/* 슬라이드 플러그인의 스타일 */
.slick-track {
  height: 475px;
  width: 100%;
  padding: 0; background: #000
}



/* .app-container에 대한 스타일 */
.app-container {

  overflow: hidden;  
  height: 1000px;
}

/* header에 대한 스타일 */
header {
  background-color: #000;
  height: 70px;
  width: 100%;
  border-bottom: 1px solid #666;
  padding-left: 1vw;  
  display: flex;
  align-items: center;
  justify-content: space-between;
}

header nav ul {
  display: flex;
  gap: 50px;
  /* width: 70vw; */
  /* border: 1px solid red; */
  justify-content: flex-end;
  /* border:1px solid red; */
  padding-right: 40px;
}

/* header ul li a에 대한 스타일 */
header nav ul li a {
  color: #fff;
  margin-top: 5px;
  font-weight: bold;
  font-size: 18px;
  display: block;
}

header ul li a:hover,
header ul li a:focus {
color: #f9b65d;
}

/* .app-container .logo img에 대한 스타일 */
.app-container .logo img {
  width: 70px;
  height: auto;
  margin-top: 10px;
}



/* main에 대한 스타일 */
main {
  /* padding: 0; */
  width: 100%;
  margin: 0 auto;
  position: relative;
  top: -70px;
  margin-top: 100px;

}

/* main .content-container에 대한 스타일 */
main .content-container {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  /* gap: 2%; */
  justify-content: space-between;
  padding-right: 0;
}

/* .news-menu에 대한 스타일 */
.news-menu {
  width: 32.5%;
}

/* .item-list에 대한 스타일 */
.item-list {
  list-style: none;
  /* padding: 0; */
  /* padding-right: 20px; */
 

}

/* .item-list li에 대한 스타일 */
.item-list li a {
  font-size: 14px;
  height: 41px;
  line-height: 41px;
  border-bottom: 1px solid #ccc;
  display: block;
 
}

/* a.item-link에 대한 스타일 */
a.item-link {
  color: inherit;
  text-decoration: none;
}

a.item-link:hover, 
a.item-link:focus {
  /* text-decoration: underline; */
  border-bottom: 1px solid #000;
}

/* span.update-text에 대한 스타일 */
span.update-text {
  color: #999;
}

/* .recommended-menu에 대한 스타일 */
.recommended-menu {
  width: 32.5%;
}
.recommended-menu img {width: 100%;height: 210px;}
/* .game에 대한 스타일 */
.game {
  width: 32.5%;
}
.game img {width: 100%;height: 210px;}
/* h2에 대한 스타일 */
h2 {
  font-size: 24px;
  font-weight: bold;
  color: #333;
  margin-bottom: 30px;
}

/* footer에 대한 스타일 */
footer {
  position: absolute;
  bottom: -70px;
  border-top: 1px solid #ddd;
  width: 100%;

}

/* footer p에 대한 스타일 */
footer p {
  text-align: center;
  font-size: 12px;
  color: #999;
  margin-top: 20px;
}
